.form {
  display: flex;
  justify-content: space-between;
}

.form__description {
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  letter-spacing: -0.005em;
  color: #ffffff;
  mix-blend-mode: normal;
  margin: 0;
  margin-bottom: 19px;
  padding-top: 4px;
}

.form input {
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: normal;
  background: #ffffff;
  border: 1px solid #c9ccd4;
  box-sizing: border-box;
  border-radius: 4px;
  min-height: 48px;
  margin-bottom: 20px;
  padding-left: 28px;
  width: 100%;
  height: 48px;
  font-size: 15px;
  line-height: 24px;
}

.form input:hover,
.form input:focus {
  background: #e0ffff;
}

.form input::-webkit-input-placeholder {
  font: inherit;
  color: #1c1c1c;
  opacity: 0.5;
}

.form button {
  background: #0ad9c6;
  border-radius: 6px;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 14px;
  text-align: center;
  color: #000000;
  border: none;
  cursor: pointer;
  min-height: 50px;
  width: 100%;
  letter-spacing: 0.025em;
}

.form button:hover {
  background: transparent;
  border: 1px solid #ffffff;
  color: #ffffff;
}

@media (min-width: 320px) and (max-width: 1023px) {
  .form__description {
    color: #000000;
    font-size: 18px;
    margin-top: 65px;
    margin-bottom: 22px;
  }

  .form input {
    margin-bottom: 20px;
  }

  .form {
    padding: 0 15px;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .form__description {
    margin-bottom: 30px;
  }

  .form input::-webkit-input-placeholder {
    padding-left: 18px;
  }
}
